body {
  margin: 0;
  padding: 0;
  font-family: sans-serif;
  background-color: #fff9c4;
}
.card-content h3 {
  font-size: 1.4rem;
}
.row {
  display: flex;
  flex-wrap: wrap;
}
.contenedor-noticias {
  padding: 2rem;
  border-radius: 1rem;
  margin-top: 3rem;
}
.card-title{
    line-height: 1.2em;
    min-height: calc((1.2em * 4) + 8px);
    max-height: calc((1.2em * 4) + 8px);
    overflow: hidden;
    text-overflow: ellipsis;
    display: -webkit-box !important;
     -webkit-line-clamp: 4;
}
.card-content p{
    line-height: 1.2em;
    min-height: calc(1.2em * 4);
    max-height: calc(1.2em * 4);
    overflow: hidden;
    text-overflow: ellipsis;
    display: -webkit-box !important;
    -webkit-line-clamp: 4;
}
.card-image{
    position: relative;
    min-height: 150px;
    max-height: 150px;
    width: auto;
    overflow: hidden;
}
.card .card-image img{
    position: absolute;
    left:50%;
    transform: translateX(-50%);
    height: 100%;
    width: auto;
}
.buscador {
  margin-top: 2rem;
  margin-bottom: 2rem;
  text-align: left;
}
.buscador label {
  font-size: 2rem;
  color: black;
}
.dropdown-content li>span {
  color: black;
}
.select-wrapper input.select-dropdown:focus {
  border-bottom: 1px solid #e64a19!important;
}
h2 {
  font-family: 'Raleway', sans-serif;
  font-weight: 900;
  font-size: 2rem;
  text-transform: uppercase;
  text-align: center;
}
.enviar input[type="submit"] {
  width: 100%;
}

.fade-enter{
    opacity: 0;
    transform: scale(0.75);
}
.fade-enter.fade-enter-active{
    opacity: 1;
    transition: all .5s ease-in;
    transform: scale(1);
}
.fade-exit{
    opacity: 1;
    transform: scale(1);
}
.fade-exit.fade-exit-active{
    opacity: 0;
    transition: all .5s ease-in;
    transform: scale(0.75);
}
